<template>
  <div class="send-message-com">
    <div class="send-side">
      <el-input v-model="sendText" class="sendMsg" placeholder="请输入内容，按回车发送"></el-input>
      <el-button type="primary" @click="sendMsg">发送(S)</el-button>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    userID: {
      type: Number,
      required: true,
      default: () => -1
    }
  },
  data() {
    return {
      sendText: ""
    };
  },
  methods: {
    sendMsg() {
      console.log(this.userID,'iiiiii')
      const former = new FormData();
      former.append("userID", this.userID);
      former.append("content", this.sendText);
      this.$api.sendMessage(former).then(resp => {
        console.log(resp, "success");
      });
      this.sendText = "";
    }
  }
};
</script>

<style scoped lang="scss">
.send-message-com {
  .send-side {
    display: flex;
    .sendMsg {
    }
  }
}
</style>


